@import url(https://rsms.me/inter/inter.css);
:root {
    --fill: var(--light);
    --stroke: var(--dark);
    --highlight: var(--attention);
    --circle: var(--highlight);
}

html, body {
    overflow: hidden;
}

body {
    background-color: var(--mid);
}

.App {
  display: grid;
  justify-items: center;
  height: 100vh;
  width: 100vw;
  align-items: center;
  align-content: center;
  padding: calc(var(--tile) * 2);
}

[type="range"] {
    width: 100%;
}

[type="number"] {
    width: 75px;
    position: absolute;
    bottom: calc(2 * var(--tile));
    left: calc(2 * var(--tile));
    background: var(--attention);
    background: var(--highlight);
    border: 2px solid var(--attention);
    border: 2px solid var(--highlight);
    border-radius: 4px;
}

.windowAction {
    min-height: calc(1.5 * var(--tile));
    color: var(--attention);
    background: transparent;
    border: 1px solid var(--attention);
    min-width: calc(1.5 * var(--tile));
    cursor: pointer;
}

.windowAction.-close {
    position: absolute;
    top: calc(2 * var(--tile));
    left: calc(2 * var(--tile));
}

.windowAction.-open {
    position: absolute;
    bottom: calc(2 * var(--tile));
    right: calc(2 * var(--tile));
}
svg {
    width: 100%;
    stroke-width: 3.5;
}

path {
    stroke: var(--stroke);
    fill: var(--fill);
}

circle {
    fill: var(--circle);
}

.isClosed, .isExtended {
    cursor: pointer;
}

.isExtended :nth-child(2) {
    opacity: 0;
}
.isClosed :nth-child(1) {
    opacity: 0;
}

circle.lowlight {
    opacity: 0;
    transition: opacity 100ms, stroke-width 100ms, -webkit-transform 100ms;
    transition: opacity 100ms, transform 100ms, stroke-width 100ms;
    transition: opacity 100ms, transform 100ms, stroke-width 100ms, -webkit-transform 100ms;
    -webkit-transform: translateY(10%);
            transform: translateY(10%);
}

circle.highlight {
    -webkit-transform: translateY(0%);
            transform: translateY(0%);
    opacity: 1;
}

text {
    stroke-width: 0;
    fill: black;
}

.keys {
    display: flex;
}

.keys > * {
    white-space: nowrap;
    flex: 1 1;
    text-align: center;
}
article {
    display: flex;
}

article > .hand {
    max-width: 320px;
}

